<template>
    <div class="order">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="order-main">
            <template v-if="isList">
        
            </template>
            <template v-else>
                <h1 class="order-tips">{{msg}}</h1>
            </template>
        </div>
        <elm-footer></elm-footer>
    </div>
</template>
    
    <script>
    export default {
        name:"order",
        data() {
            return {
                fatherName:'订单列表',
                msg:"您还没有订单信息，有喜欢的就赶快下手吧！",
                isList:false,
            }
        },
    }
    </script>

    <style >
    .order-main{
        padding:0.4rem 0.2rem;
    }
    .order-tips{
        text-align: center;
        font-size:.3rem;
        color:#999;
        letter-spacing: 0.04rem;
    }
    
    
    </style>


