            userIntegral:200,
            totalPrice:0,
            carNumber:0,
            address:[
                {
                    marker:"公司",
                    content:"中关村软件园9号楼吸烟区",
                    tel:"15039405397",
                    name:"代瑞英",
                    sex:"女士",
                    default:true,
                },
                {
                    marker:"家",
                    content:"东沙各庄",
                    tel:"15039405397",
                    name:"代代",
                    sex:"女士",
                    default:false,
                }
            ],
